.worksContentArea {
    margin: auto;
    padding: 2rem;
    max-width: 1500px;
}


.worksContentArea a{
    text-decoration: none;
    color: #200050;
}

.contentAreaBox {
    margin: auto;
    display: grid;
    gap: 0 2%;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.workContentCard {
    background-size: cover;
    min-width: 6em;
    position: relative;
}


.workContentThmbnailWrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
}
.workContentThmbnailWrapper:before {
    content: "";
    display: block;
    padding-top: 100%;
}

.workContentThmbnail {
    position: absolute;
    background-size: cover;
    background-position: center;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.workContentTitle {
    font-family: sans-serif;
    font-weight: bold;
    padding-bottom: 2em;
}
