@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Teko&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 img {
  border-radius:50%;
  width:50px;
  height:50px;
}
ul.topnav {
	width: 100%;
	height: 55px;
	overflow: hidden;
	box-shadow: 0 0 5px gray;
	margin: 0;
	padding: 0;
	list-style-type: none;
	background-color: #ffffff;
	position: fixed;
	top: 0px;
}
ul.topnav li {
	float: left;
}
ul.topnav li a {
	display: block;
	padding: 17.5px 16px;
	text-align: center;
	text-decoration: none;
	color: #130068;
}
ul.topnav li a:hover:not(.active) {
	background-color: #a9bce200;
}
ul.topnav li a.active {
	background-color: #da3c41;
}
ul.topnav li.right {
	float: right;
}
@media screen and (max-width: 480px) {
	ul.topnav li.right, ul.topnav li {
		float: none;
	}
}

ul.topnav li.center
{
	width: 100px;
	top: 50%;
	left: 50%;
	transform: translateY(-50%) translateX(-50%);
	padding: 0;
	position:absolute;
}
ul.topnav li.center a:hover:not(.active) 
{
	background-color: #ffffff00;
}
ul.topnav li.home
{
	width: 100px;
	top: 50%;
	left: 0%;
	transform: translateY(-50%) translateX(0%);
	padding: 0;
	position:absolute;
}

ul.topnav li font.home
{
	font-family: 'Teko', sans-serif;
	color: #130068;
	text-align: center;
	font-size:25px;
}
.worksContentArea {
    margin: auto;
    padding: 2rem;
    max-width: 1500px;
}


.worksContentArea a{
    text-decoration: none;
    color: #200050;
}

.contentAreaBox {
    margin: auto;
    display: grid;
    grid-gap: 0 2%;
    gap: 0 2%;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.workContentCard {
    background-size: cover;
    min-width: 6em;
    position: relative;
}


.workContentThmbnailWrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
}
.workContentThmbnailWrapper:before {
    content: "";
    display: block;
    padding-top: 100%;
}

.workContentThmbnail {
    position: absolute;
    background-size: cover;
    background-position: center;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.workContentTitle {
    font-family: sans-serif;
    font-weight: bold;
    padding-bottom: 2em;
}

* {
  margin: 0px;
  padding: 0px;
}

div#pc
{
  display: inline;
}
div#phone
{
  display: none;
}


 
div.page{
  position:absolute;
  -ms-overflow-style: none;
  top: 55px;
  width: 100%;
  background-color: #eeeeee;
}


::-webkit-scrollbar {
  display: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


button.MenuButton
{
  padding: 0;
  border: none;
  background: transparent;
}

.HamburgerButton
{
  position: fixed;
  display: block;
  top: 8px;
  left: 12px;
  border: none;
  background: transparent;
  width: 40px;
  height: 40px;
}

.OpenMenu
{
  display: none;
  position: fixed;
  background-color: rgba(255, 255, 255, 0.85);
  width: 100vw;
  height: 100vh;
  top: 0px;
}
.OpenMenuArea
{
  list-style: none;
  padding: 5em;
}
.OpenMenuArea li a
{
  text-decoration: none;
}
.OpenMenuFont
{
  color: #130068;
  font-family: 'Teko', sans-serif;
  display: block;
  margin: auto;
  text-align: center;
  font-size: 2em;
}

div.list
{
  display: grid;
    grid-template-columns: 100px 100px 100px;
    grid-template-rows: 100px 100px 100px;
}

div.list img
{
  width: 100%;
  height: 100%;
}

.contentArea {
  width: 100vw;
  background: rgba(238, 238, 238, 0);
  padding-top: 3em;
  padding-bottom: 3em;
}

.workPageBox {
  background: rgba(238, 238, 238, 0);
  margin: auto;
}
.workPageCard {
  background: #fff;
  width: min(100%, 90em);
  margin: auto;
}

.warkBg
{
  -ms-overflow-style: none;
  height: 100%;
  width: 100vw;
  background-size: cover;
  background-position: 70% 0%;
  z-index: -1;
}
.info {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  max-width: 100%;
  padding-top: 10%;
  padding-bottom: 5%;
}

.sliderArea
{
  flex: 0 0 100%;
  max-width: 100%;
  margin-top: 15px;
  padding-right: 100px;
  padding-left: 100px;
  margin-right: auto;
  margin-left: auto;
}

.videoArea
{
  text-align: center;
  width: 50%;
  height: 50%;
}
.videoFrame
{
  border-radius: 50px 50px 50px 50px;
  color: #eee;
  flex: 1 1 auto;
  padding-top: 56.25%;
  padding: 1.25rem;
}

.videoFrame video {
  text-align: center;
  width: 100%;
  height: 100%;
}

.gameImage{
  width: 100%;
  height: 100%;
}

.bottomIcon
{
  width: 100px;
  margin: 15px;
  border-radius: 50%;
}
@media screen and (max-width: 430px) {
  .bottomIcon
  {
    width: 75px;
    margin: 15px;
    border-radius: 50%;
  }
}
@media screen and (max-width: 360px) {
  .bottomIcon
  {
    width: 45px;
    margin: 15px;
    border-radius: 50%;
  }
}
@media screen and (max-width: 265px) {
  .bottomIcon
  {
    width: 30px;
    margin: 15px;
    border-radius: 50%;
  }
}


.bottomArea
{
  border:10px solid #201452;
  padding:10px;
  text-align:center;
  background: #201452;
}
.bottomArea>center{
  padding : 20px ;
}

.homeBg {
    -ms-overflow-style: none;
    height: 100vmax;
    width: 100vw;
    background-size: cover;
    background-position: 70% 0%;
    z-index: -1;
}
.homeRadius
{
  top: 6em;
  left: 2em;
  padding: 0.7em 1em;
  position:absolute;
  border-radius: 0px 50px 0px 50px;
  background: rgba(0, 0, 0, 0.7);
  color: #eee;
}

.header {
  height: min(18em, 50vw);
  overflow: hidden;
  background-size: cover;
  background-position: 70% 0%;
  z-index: -1;
  border-bottom: 1px solid #eee;
  box-sizing: border-box;
}

.headlineProfile {
  position: relative;
  background: #fff;
  border-bottom: 1px solid #eee;
  display: flex;
}

.headlineContentProfile {
  width: 50em;
  max-width: 90%;
  height: 100%;
  margin: auto;
}
.profileIconWrapper {
  width: min(10em, max(20vw, 5em));
  height: min(10em, max(20vw, 5em));
}

.biographyArea {
  padding: 8px;
}

.profileIcon {
  margin-top: -50%;
  width: 100%;
  height: 100%;
  display: block;
  border-radius: 50%;
  background-size: contain;
  border: solid 2px rgb(0, 0, 0);
  background: #fff;
}

.name {
  font-family: 'Teko', sans-serif;
  color: #130068;
  font-size: 45px;
}

.textArea  {
  flex: 0 0 100%;
  max-width: 100%;
  margin-top: 15px;

  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.textFrame {
  border-radius: 50px 50px 50px 50px;
  background: #596888;
  color: #eee;

  flex: 1 1 auto;
  padding: 1.25rem;
}

@media screen and (max-width: 760px) {
  div#pc
  {
    display: none;
  }
  div#phone
  {
    display: inline;
  }
}
