ul.topnav {
	width: 100%;
	height: 55px;
	overflow: hidden;
	box-shadow: 0 0 5px gray;
	margin: 0;
	padding: 0;
	list-style-type: none;
	background-color: #ffffff;
	position: fixed;
	top: 0px;
}
ul.topnav li {
	float: left;
}
ul.topnav li a {
	display: block;
	padding: 17.5px 16px;
	text-align: center;
	text-decoration: none;
	color: #130068;
}
ul.topnav li a:hover:not(.active) {
	background-color: #a9bce200;
}
ul.topnav li a.active {
	background-color: #da3c41;
}
ul.topnav li.right {
	float: right;
}
@media screen and (max-width: 480px) {
	ul.topnav li.right, ul.topnav li {
		float: none;
	}
}

ul.topnav li.center
{
	width: 100px;
	top: 50%;
	left: 50%;
	transform: translateY(-50%) translateX(-50%);
	padding: 0;
	position:absolute;
}
ul.topnav li.center a:hover:not(.active) 
{
	background-color: #ffffff00;
}
ul.topnav li.home
{
	width: 100px;
	top: 50%;
	left: 0%;
	transform: translateY(-50%) translateX(0%);
	padding: 0;
	position:absolute;
}

ul.topnav li font.home
{
	font-family: 'Teko', sans-serif;
	color: #130068;
	text-align: center;
	font-size:25px;
}